import { Component, Vue } from "vue-property-decorator";
import HeaderTab from "@/components/common/HeaderTab.vue";
import BodyTab from "@/components/common/BodyTab.vue";
import FooterTab from "@/components/common/FooterTab.vue";

@Component({
  components: {
    HeaderTab,
    BodyTab,
    FooterTab,
  },
})
export default class Home extends Vue {
  private searchValue = "";
  private activeTab: any = 0;
  private tabItems: any = ["Hot", "Soon"];
  private recommendMovies: any = [];
  private InitialMovies: any = [];
  private TotalMovies: any = 0;

  /*
    * @Author: keanu
    * @LastAuthor: null
    * @Param: val: any
    * @Return: null
    * @Date: 2021-06-04 10:04:13
    * @Description: search a movie
  */
  private onSearch(val: any) {
    console.log(val);
  }

  /*
    * @Author: keanu
    * @LastAuthor: null
    * @Param: index: any
    * @Return: null
    * @Date: 2021-06-04 13:55:44
    * @Description: change tab and get data
  */
  private onChangeTab(index: any) {
    this.getDataByCategory(Number(index + 1));
  }

  /*
    * @Author: keanu
    * @LastAuthor: null
    * @Param: null
    * @Return: Array|[]
    * @Date: 2021-06-04 14:08:31
    * @Description: fetch all data from api
  */
  private getData() {
    let params = {
      request: 1,
      data: {}
    };

    this.$http.post(this.$api.MovieService, params)
      .then((res: any) => {
        this.TotalMovies = res.data.length;
      })
      .catch((error: any) => {
        console.log(error);
      });
  }

  /*
    * @Author: keanu
    * @LastAuthor: null
    * @Param: catID: any
    * @Return: Array|[]
    * @Date: 2021-06-04 14:17:02
    * @Description: fetch all data from api using the category id
  */
  private getDataByCategory(catID: any) {
    let params = {
      request: 2,
      data: {
        category: catID
      }
    };

    this.$http.post(this.$api.MovieService, params)
      .then((res: any) => {
        if (catID == 3) {
          this.recommendMovies = [];
          res.data.map((item: any) => {
            this.recommendMovies.push({
              img: "icon1",
              title: item.Title,
              description: item.Description,
              rate: Number(item.Rate)
            })
          })
        } else {
          this.InitialMovies = [];
          res.data.map((item: any) => {
            this.InitialMovies.push({
              img: "icon1",
              title: item.Title,
              description: item.Description,
              rate: Number(item.Rate)
            })
          })
        }
      })
      .catch((error: any) => {
        console.log(error);
      });
  }

  private async created() {
    await this.getData();
    await this.getDataByCategory(1);
    await this.getDataByCategory(3);
  }

}
