/* eslint-disable*/
import axios from "axios";

const instance = axios.create({
  baseURL: "https://kathdelacruz.com/vue-heren-test-server",
  // baseURL: "http://localhost/vue-heren-test-server",
  timeout: 3000,
});

class http {
  static async get(url: any, params: any) {
    return await instance.get(url, { params });
  }

  static async post(url: any, params: any) {
    return await instance.post(url, JSON.stringify(params));
  }
}

export default http;
